body {
    display: grid;
    height: 100vh;
    grid-template-rows: auto 1fr auto;
  }
  
  
  header {
    background: ffd8a8;
    padding: 2rem;
  }
  
  main {
    background: #fff4e6;
  }
  
  footer {
    background: wheat;
    padding: 2rem;
    text-align: center;
  }
  
  body {
    font-family: system-ui, sans-serif;
  }