body {
    display: grid;
    height: 100vh;
    grid-template-rows: auto 1fr auto;
  }
  
  
  header {
    background: #0c8599;
    padding: 1rem;
    padding-top: 10px;
    padding-left: 14px;
    padding-bottom: 22px;
  }
  
  main {
    background: #38d9a9;
  }
  
  footer {
    background: wheat;
    padding: 2rem;
    text-align: center;
  }
  
  body {
    font-family: system-ui, sans-serif;
  }